import { Accordion, AccordionDetails, AccordionSummary, Box, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import Helmet from 'react-helmet';
import { TwitterIcon, TwitterShareButton } from 'react-share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import Layout from '../../components/layout';
import PageNavigation from '../../components/page-navigation';

const useStyles = makeStyles(theme => ({
  heading: {
    backgroundColor: "#ddffff"
  },
  paragraph: {
    display: "block",
    textAlign: "left",
    backgroundColor: "#eeffff"
  }
}));

const AnswerNormal = () => {

  const classes = useStyles();

  return (
    <Layout>
      <section id="header">
        <div className="inner">
          <h1>正解です！</h1>
        </div>
      </section>
      <Helmet title="クリア | 氷蔵姉妹の挑戦" />
      <section id="one" className="main style1">
        <div className="grid-wrapper">
          <div className="col-12">
            <p>
              おめでとうございます！クリアです！
            </p>
            <p>
              遊んでいただきありがとうございました！<br />
                もしよろしければ、 #氷蔵姉妹の挑戦 のタグをつけて、感想を呟いて頂けると嬉しいです！
            </p>
            <div>
              <div>結果をシェアする</div>
              <TwitterShareButton
                url="https://contents.reizouko.online/riddle20200831/"
                title="謎解き (ノーマルモード) をクリアしました！"
                hashtags={["氷蔵姉妹の挑戦"]}
                className="custom-button"
              >
                <TwitterIcon size={48} round />
              </TwitterShareButton>
            </div>
            <Box mt={5} mb={5}>
              <Accordion>
                <AccordionSummary expandIcon={<ExpandMoreIcon />} className={classes.heading}>
                  <Typography>簡単すぎてちょっと物足りないな…というあなたへ</Typography>
                </AccordionSummary>
                <AccordionDetails className={classes.paragraph}>
                  <p>
                    あなたがたどり着いた、この『れいとうこ』という答え、<br />
                    これ実は、難易度ノーマルの答えなのです。
                    </p>
                  <p>
                    難易度ハードの答えが別にあります。同じ問題から別の答えが導かれます。<br />
                    前のページに戻って問題を眺めなおして見てください。<br />
                    何か違和感が…<br />
                    その違和感は、きっとハードモードにつながる鍵となるでしょう。
                  </p>
                  <p>
                    ハードモードのヒントを見る場合は、最終問題の回答を入れるテキストボックスに<br/>
                    『はーどもーどひんと』<br/>
                    とひらがなで入れてください。
                  </p>
                </AccordionDetails>
              </Accordion>
            </Box>
          </div>
        </div>
        <div className="grid-wrapper">
          <div className="col-12">
            <PageNavigation prev="/question/" />
          </div>
        </div>
      </section>
    </Layout>
  )
};

export default AnswerNormal;